import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './Leaderboard.css';

interface UserRanking {
  cedula: string;
  name: string;
  username: string;
  coins: number;
  exercisesCompleted: number;
}

const Leaderboard: React.FC = () => {
  const navigate = useNavigate();
  const [users, setUsers] = useState<UserRanking[]>([]);
  const [timeFilter, setTimeFilter] = useState<'all' | 'month' | 'week'>('all');
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    // Simular una carga
    setLoading(true);
    setTimeout(() => {
      loadUsers();
      setLoading(false);
    }, 800);
  }, [timeFilter]);

  const loadUsers = () => {
    // Obtener todos los usuarios registrados (basados en localStorage)
    const allUsers: UserRanking[] = [];
    
    // Buscar en localStorage todas las claves que tengan el formato 'user_coins_cedula'
    Object.keys(localStorage).forEach(key => {
      if (key.startsWith('user_coins_')) {
        const cedula = key.replace('user_coins_', '');
        let userCoins = parseInt(localStorage.getItem(key) || '0', 10);
        
        // Intentar obtener información adicional del usuario
        let userData = { name: '', username: '' };
        try {
          const userRaw = localStorage.getItem('math_basics_users');
          if (userRaw) {
            const users = JSON.parse(userRaw);
            const userMatch = users.find((u: any) => u.cedula === cedula);
            if (userMatch) {
              userData.name = userMatch.name || '';
              userData.username = userMatch.username || cedula;
            }
          }
        } catch (error) {
          console.error('Error al cargar datos de usuario:', error);
        }
        
        // Obtener la cantidad de ejercicios completados
        let exercisesCompleted = 0;
        try {
          const historyRaw = localStorage.getItem(`exercise_history_${cedula}`);
          if (historyRaw) {
            const history = JSON.parse(historyRaw);
            // Aplicar filtro de tiempo si es necesario
            let filteredHistory = history;
            if (timeFilter !== 'all') {
              const now = new Date();
              const startDate = new Date();
              if (timeFilter === 'month') {
                startDate.setMonth(now.getMonth() - 1);
              } else if (timeFilter === 'week') {
                startDate.setDate(now.getDate() - 7);
              }
              
              filteredHistory = history.filter((h: any) => {
                const exerciseDate = new Date(h.date);
                return exerciseDate >= startDate;
              });

              // Recalcular monedas si hay filtro de tiempo
              const coinsInPeriod = filteredHistory
                .filter((h: any) => h.correct)
                .reduce((sum: number, h: any) => sum + h.coins, 0);
              
              // Usar solo las monedas del período filtrado
              userCoins = coinsInPeriod;
            }
            
            exercisesCompleted = filteredHistory.length;
          }
        } catch (error) {
          console.error('Error al cargar historial de ejercicios:', error);
        }
        
        // Agregar usuario al ranking si tiene monedas
        if (userCoins > 0 || exercisesCompleted > 0) {
          allUsers.push({
            cedula,
            name: userData.name,
            username: userData.username,
            coins: userCoins,
            exercisesCompleted
          });
        }
      }
    });
    
    // Ordenar por monedas (descendente)
    allUsers.sort((a, b) => b.coins - a.coins);
    setUsers(allUsers);
  };

  const getOrdinal = (position: number): string => {
    switch (position) {
      case 1: return '1º';
      case 2: return '2º';
      case 3: return '3º';
      default: return `${position}º`;
    }
  };

  const handleGoBack = () => {
    navigate('/'); // Navegar a la página principal
  };

  return (
    <div className="leaderboard-container">
      <div className="leaderboard-header">
        <div className="header-content">
          <h2>Ranking de Estudiantes</h2>
          <button 
            className="back-button"
            onClick={handleGoBack}
          >
            ← Volver a ejercicios
          </button>
        </div>
        <div className="filter-buttons">
          <button 
            className={`filter-button ${timeFilter === 'all' ? 'active' : ''}`}
            onClick={() => setTimeFilter('all')}
          >
            Todo el tiempo
          </button>
          <button 
            className={`filter-button ${timeFilter === 'month' ? 'active' : ''}`}
            onClick={() => setTimeFilter('month')}
          >
            Último mes
          </button>
          <button 
            className={`filter-button ${timeFilter === 'week' ? 'active' : ''}`}
            onClick={() => setTimeFilter('week')}
          >
            Última semana
          </button>
        </div>
      </div>
      
      {loading ? (
        <div className="loading-indicator">
          <div className="spinner"></div>
          <p>Cargando ranking...</p>
        </div>
      ) : users.length === 0 ? (
        <div className="empty-ranking">
          <p>No hay estudiantes en el ranking actual</p>
        </div>
      ) : (
        <div className="ranking-table-container">
          <table className="ranking-table">
            <thead>
              <tr>
                <th className="position-column">Pos.</th>
                <th>Estudiante</th>
                <th className="exercises-column">Ejercicios</th>
                <th className="coins-column">Monedas</th>
              </tr>
            </thead>
            <tbody>
              {users.map((user, index) => (
                <tr key={user.cedula} className={index < 3 ? `top-${index + 1}` : ''}>
                  <td className="position-column">
                    <div className={`position-badge ${index < 3 ? `top-${index + 1}` : ''}`}>
                      {getOrdinal(index + 1)}
                    </div>
                  </td>
                  <td>
                    <div className="user-data">
                      <span className="username">{user.username}</span>
                      {user.name && <span className="name">{user.name}</span>}
                    </div>
                  </td>
                  <td className="exercises-column">{user.exercisesCompleted}</td>
                  <td className="coins-column">
                    <div className="coins-display">
                      {user.coins} <span role="img" aria-label="moneda">🪙</span>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default Leaderboard; 