import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';
import './Store.css';

interface StoreItem {
  id: string;
  name: string;
  description: string;
  price: number;
  image: string;
  category: string;
  availableCount?: number;
}

const Store: React.FC = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [coins, setCoins] = useState<number>(0);
  const [items, setItems] = useState<StoreItem[]>([]);
  const [purchaseHistory, setPurchaseHistory] = useState<any[]>([]);
  const [activeCategory, setActiveCategory] = useState<string>('all');
  const [isBlockchainEnabled, setIsBlockchainEnabled] = useState<boolean>(false);
  const [message, setMessage] = useState<{ text: string; type: 'success' | 'error' | 'info' | null }>({ text: '', type: null });

  useEffect(() => {
    if (!user) {
      navigate('/login');
      return;
    }

    // Cargar monedas del usuario
    const userCoins = localStorage.getItem(`user_coins_${user.cedula}`);
    if (userCoins) {
      setCoins(parseInt(userCoins, 10));
    }

    // Cargar historial de compras
    const history = localStorage.getItem(`purchase_history_${user.cedula}`);
    if (history) {
      setPurchaseHistory(JSON.parse(history));
    }

    // Cargar items de la tienda
    loadStoreItems();
  }, [user, navigate]);

  const loadStoreItems = () => {
    // En una implementación real, esto vendría de una API
    const storeItems: StoreItem[] = [
      {
        id: 'physical1',
        name: 'Chocolate',
        description: 'Una barra de chocolate para recompensar tu esfuerzo',
        price: 50,
        image: '🍫',
        category: 'physical',
        availableCount: 10
      },
      {
        id: 'physical2',
        name: 'Bolígrafo especial',
        description: 'Bolígrafo de la facultad para tus anotaciones',
        price: 30,
        image: '🖊️',
        category: 'physical',
        availableCount: 5
      },
      {
        id: 'digital1',
        name: 'Pista para ejercicios',
        description: 'Obtén una pista para resolver cualquier ejercicio',
        price: 10,
        image: '💡',
        category: 'digital'
      },
      {
        id: 'digital2',
        name: 'Ejercicios premium',
        description: 'Acceso a ejercicios avanzados por una semana',
        price: 25,
        image: '📚',
        category: 'digital'
      },
      {
        id: 'nft1',
        name: 'Insignia NFT: Matemático',
        description: 'Insignia digital que certifica tus habilidades matemáticas',
        price: 100,
        image: '🏆',
        category: 'blockchain'
      },
      {
        id: 'nft2',
        name: 'OroToken',
        description: 'Token intercambiable en la blockchain',
        price: 40,
        image: '🪙',
        category: 'blockchain'
      }
    ];

    setItems(storeItems);
  };

  const purchaseItem = (item: StoreItem) => {
    if (coins < item.price) {
      setMessage({ 
        text: 'No tienes suficientes monedas para esta compra', 
        type: 'error' 
      });
      return;
    }
    
    // Si es un artículo físico, verificar disponibilidad
    if (item.category === 'physical' && item.availableCount !== undefined) {
      if (item.availableCount <= 0) {
        setMessage({ 
          text: 'Este artículo no está disponible actualmente', 
          type: 'error' 
        });
        return;
      }
    }
    
    // Si es un artículo blockchain y blockchain no está activado
    if (item.category === 'blockchain' && !isBlockchainEnabled) {
      setMessage({ 
        text: 'La funcionalidad blockchain no está disponible actualmente', 
        type: 'info' 
      });
      return;
    }

    // Proceso de compra
    const newCoins = coins - item.price;
    setCoins(newCoins);
    
    // Guardar monedas actualizadas
    if (user) {
      localStorage.setItem(`user_coins_${user.cedula}`, newCoins.toString());
    }
    
    // Crear registro de compra
    const purchase = {
      id: `purchase_${Date.now()}`,
      itemId: item.id,
      itemName: item.name,
      price: item.price,
      date: new Date().toISOString(),
      category: item.category
    };
    
    // Actualizar historial de compras
    const updatedHistory = [...purchaseHistory, purchase];
    setPurchaseHistory(updatedHistory);
    
    // Guardar historial actualizado
    if (user) {
      localStorage.setItem(`purchase_history_${user.cedula}`, JSON.stringify(updatedHistory));
    }
    
    // Actualizar conteo de artículos físicos
    if (item.category === 'physical' && item.availableCount !== undefined) {
      const updatedItems = items.map(i => {
        if (i.id === item.id) {
          return { ...i, availableCount: (i.availableCount as number) - 1 };
        }
        return i;
      });
      setItems(updatedItems);
    }
    
    setMessage({ 
      text: `¡Has comprado ${item.name} exitosamente!`, 
      type: 'success' 
    });
  };

  const filterItemsByCategory = (category: string) => {
    setActiveCategory(category);
  };

  const getFilteredItems = () => {
    if (activeCategory === 'all') {
      return items;
    }
    return items.filter(item => item.category === activeCategory);
  };

  const handleGoBack = () => {
    navigate('/'); // Navegar a la página principal
  };

  return (
    <div className="store-container">
      <div className="store-header">
        <div className="header-content">
          <h2>Tienda Virtual</h2>
          <button 
            className="back-button"
            onClick={handleGoBack}
          >
            ← Volver a ejercicios
          </button>
        </div>
        <div className="store-coins">
          <span role="img" aria-label="moneda">🪙</span> {coins} monedas
        </div>
      </div>

      {message.text && (
        <div className={`message ${message.type}`}>
          {message.text}
          <button 
            className="message-close"
            onClick={() => setMessage({ text: '', type: null })}
          >
            ×
          </button>
        </div>
      )}

      <div className="store-categories">
        <button 
          className={`category-button ${activeCategory === 'all' ? 'active' : ''}`}
          onClick={() => filterItemsByCategory('all')}
        >
          Todos
        </button>
        <button 
          className={`category-button ${activeCategory === 'digital' ? 'active' : ''}`}
          onClick={() => filterItemsByCategory('digital')}
        >
          Digitales
        </button>
        <button 
          className={`category-button ${activeCategory === 'physical' ? 'active' : ''}`}
          onClick={() => filterItemsByCategory('physical')}
        >
          Físicos
        </button>
        <button 
          className={`category-button ${activeCategory === 'blockchain' ? 'active' : ''}`}
          onClick={() => filterItemsByCategory('blockchain')}
        >
          Blockchain
        </button>
      </div>

      <div className="store-items">
        {getFilteredItems().map(item => (
          <div key={item.id} className={`store-item ${item.category}`}>
            <div className="store-item-image">
              <span role="img" aria-label={item.name}>{item.image}</span>
            </div>
            <div className="store-item-details">
              <h3>{item.name}</h3>
              <p>{item.description}</p>
              {item.category === 'physical' && item.availableCount !== undefined && (
                <p className="item-availability">
                  Disponibles: {item.availableCount}
                </p>
              )}
              <div className="item-price">
                <span role="img" aria-label="moneda">🪙</span> {item.price}
              </div>
            </div>
            <button 
              className="purchase-button"
              onClick={() => purchaseItem(item)}
              disabled={
                coins < item.price || 
                (item.category === 'physical' && item.availableCount !== undefined && item.availableCount <= 0) ||
                (item.category === 'blockchain' && !isBlockchainEnabled)
              }
            >
              Comprar
            </button>
          </div>
        ))}
      </div>

      <div className="purchase-history">
        <h3>Historial de Compras</h3>
        {purchaseHistory.length === 0 ? (
          <p className="no-history">Aún no has realizado ninguna compra.</p>
        ) : (
          <table className="history-table">
            <thead>
              <tr>
                <th>Fecha</th>
                <th>Artículo</th>
                <th>Precio</th>
                <th>Categoría</th>
              </tr>
            </thead>
            <tbody>
              {purchaseHistory.slice().reverse().map(purchase => (
                <tr key={purchase.id}>
                  <td>{new Date(purchase.date).toLocaleString()}</td>
                  <td>{purchase.itemName}</td>
                  <td>{purchase.price} 🪙</td>
                  <td>
                    <span className={`category-badge ${purchase.category}`}>
                      {purchase.category === 'digital' ? 'Digital' : 
                        purchase.category === 'physical' ? 'Físico' : 'Blockchain'}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>

      {user?.isAdmin && (
        <div className="admin-controls">
          <h3>Controles de Administrador</h3>
          <div className="admin-control-row">
            <label>
              <input 
                type="checkbox" 
                checked={isBlockchainEnabled}
                onChange={() => setIsBlockchainEnabled(!isBlockchainEnabled)}
              />
              Habilitar funcionalidad Blockchain
            </label>
          </div>
        </div>
      )}
    </div>
  );
};

export default Store; 