import React, { useEffect, useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';
import './UserProfile.css';

interface Exercise {
  id: string;
  type: string;
  difficulty: string;
  date: string;
  coins: number;
  correct: boolean;
}

const UserProfile: React.FC = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [coins, setCoins] = useState<number>(0);
  const [exerciseHistory, setExerciseHistory] = useState<Exercise[]>([]);
  const [stats, setStats] = useState({
    total: 0,
    correct: 0,
    easy: 0,
    medium: 0,
    hard: 0,
    coinsEarned: 0
  });

  useEffect(() => {
    if (!user) {
      navigate('/login');
      return;
    }

    // Cargar monedas del usuario
    const userCoins = localStorage.getItem(`user_coins_${user.cedula}`);
    if (userCoins) {
      setCoins(parseInt(userCoins, 10));
    }

    // Cargar historial de ejercicios
    const history = localStorage.getItem(`exercise_history_${user.cedula}`);
    if (history) {
      const parsedHistory: Exercise[] = JSON.parse(history);
      setExerciseHistory(parsedHistory);
      
      // Calcular estadísticas
      const totalExercises = parsedHistory.length;
      const correctExercises = parsedHistory.filter(ex => ex.correct).length;
      const easyExercises = parsedHistory.filter(ex => ex.difficulty === 'easy').length;
      const mediumExercises = parsedHistory.filter(ex => ex.difficulty === 'medium').length;
      const hardExercises = parsedHistory.filter(ex => ex.difficulty === 'hard').length;
      const totalCoins = parsedHistory.reduce((sum, ex) => sum + ex.coins, 0);
      
      setStats({
        total: totalExercises,
        correct: correctExercises,
        easy: easyExercises,
        medium: mediumExercises,
        hard: hardExercises,
        coinsEarned: totalCoins
      });
    }
  }, [user, navigate]);

  const handleGoBack = () => {
    navigate('/'); // Navegar a la página principal
  };

  return (
    <div className="profile-container">
      <div className="profile-header">
        <div className="header-content">
          <h2>Perfil de Usuario</h2>
          <button 
            className="back-button"
            onClick={handleGoBack}
          >
            ← Volver a ejercicios
          </button>
        </div>
        <div className="profile-coins">
          <span role="img" aria-label="moneda">🪙</span> {coins} monedas
        </div>
      </div>

      <div className="profile-info">
        <div className="info-row">
          <span className="info-label">Nombre:</span>
          <span className="info-value">{user?.name || 'No disponible'}</span>
        </div>
        <div className="info-row">
          <span className="info-label">Usuario:</span>
          <span className="info-value">{user?.username || user?.cedula}</span>
        </div>
        <div className="info-row">
          <span className="info-label">Correo:</span>
          <span className="info-value">{user?.email || 'No disponible'}</span>
        </div>
      </div>

      <div className="profile-stats">
        <h3>Estadísticas</h3>
        <div className="stats-grid">
          <div className="stat-box">
            <span className="stat-value">{stats.total}</span>
            <span className="stat-label">Ejercicios</span>
          </div>
          <div className="stat-box">
            <span className="stat-value">{stats.correct}</span>
            <span className="stat-label">Correctos</span>
          </div>
          <div className="stat-box">
            <span className="stat-value">{Math.round((stats.correct / (stats.total || 1)) * 100)}%</span>
            <span className="stat-label">Precisión</span>
          </div>
          <div className="stat-box">
            <span className="stat-value">{stats.coinsEarned}</span>
            <span className="stat-label">Monedas Ganadas</span>
          </div>
        </div>

        <div className="difficulty-stats">
          <div className="difficulty-row">
            <span className="difficulty-label">Fácil:</span>
            <div className="difficulty-bar-container">
              <div 
                className="difficulty-bar easy" 
                style={{ width: `${(stats.easy / (stats.total || 1)) * 100}%` }}
              ></div>
            </div>
            <span className="difficulty-count">{stats.easy}</span>
          </div>
          <div className="difficulty-row">
            <span className="difficulty-label">Medio:</span>
            <div className="difficulty-bar-container">
              <div 
                className="difficulty-bar medium" 
                style={{ width: `${(stats.medium / (stats.total || 1)) * 100}%` }}
              ></div>
            </div>
            <span className="difficulty-count">{stats.medium}</span>
          </div>
          <div className="difficulty-row">
            <span className="difficulty-label">Difícil:</span>
            <div className="difficulty-bar-container">
              <div 
                className="difficulty-bar hard" 
                style={{ width: `${(stats.hard / (stats.total || 1)) * 100}%` }}
              ></div>
            </div>
            <span className="difficulty-count">{stats.hard}</span>
          </div>
        </div>
      </div>

      <div className="exercise-history">
        <h3>Historial de Ejercicios</h3>
        {exerciseHistory.length === 0 ? (
          <p className="no-history">Aún no has completado ningún ejercicio.</p>
        ) : (
          <div className="history-table-container">
            <table className="history-table">
              <thead>
                <tr>
                  <th>Fecha</th>
                  <th>Tipo</th>
                  <th>Dificultad</th>
                  <th>Resultado</th>
                  <th>Monedas</th>
                </tr>
              </thead>
              <tbody>
                {exerciseHistory.slice().reverse().map((exercise) => (
                  <tr key={exercise.id}>
                    <td>{new Date(exercise.date).toLocaleString()}</td>
                    <td>{exercise.type === 'notables' ? 'Productos Notables' : 'Orden de Operaciones'}</td>
                    <td>
                      <span className={`difficulty-badge ${exercise.difficulty}`}>
                        {exercise.difficulty === 'easy' ? 'Fácil' : 
                          exercise.difficulty === 'medium' ? 'Medio' : 'Difícil'}
                      </span>
                    </td>
                    <td>
                      <span className={`result-badge ${exercise.correct ? 'correct' : 'incorrect'}`}>
                        {exercise.correct ? 'Correcto' : 'Incorrecto'}
                      </span>
                    </td>
                    <td>
                      {exercise.correct ? (
                        <span className="coins-earned-history">
                          +{exercise.coins} <span role="img" aria-label="moneda">🪙</span>
                        </span>
                      ) : '0'}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
};

export default UserProfile; 