import React, { useState } from 'react';
import './FactorizationTutorial.css';
import { InlineMath, BlockMath } from '../utils/MathRenderer';
import 'katex/dist/katex.min.css';

// Componentes para cada sección del tutorial
const IntroductionSection: React.FC = () => (
  <div className="tutorial-page">
    <h3 className="tutorial-section-heading">¿Qué es la factorización?</h3>
    
    <div className="tutorial-definition">
      <p>
        La factorización consiste en descomponer un polinomio en un producto
        de factores más simples. En finanzas y economía, se usa para:
      </p>
    </div>
    
    <div className="tutorial-example">
      <BlockMath math="ax^2 + bx + c = a(x-r_1)(x-r_2)" />
    </div>
    
    <div className="tutorial-applications">
      <h4>Aplicaciones en finanzas:</h4>
      <ul>
        <li>
          <span className="application-title">Analizar funciones</span>
          <span className="application-description">de costo, ingreso y beneficio.</span>
        </li>
        <li>
          <span className="application-title">Encontrar puntos de equilibrio</span>
          <span className="application-description">(raíces de la función beneficio).</span>
        </li>
        <li>
          <span className="application-title">Simplificar cálculos</span>
          <span className="application-description">en modelado de escenarios.</span>
        </li>
      </ul>
    </div>
  </div>
);

const BasicRulesSection: React.FC = () => (
  <div className="tutorial-page">
    <h3 className="tutorial-section-heading">Reglas básicas de factorización</h3>
    
    <div className="factorization-rule">
      <h4>1. Factor común</h4>
      <p>Si todos los términos comparten un mismo factor, lo extraemos:</p>
      <div className="rule-example">
        <BlockMath math="ax^2 + bx = x(ax + b)" />
      </div>
    </div>
    
    <div className="factorization-rule">
      <h4>2. Diferencia de cuadrados</h4>
      <div className="rule-example">
        <BlockMath math="a^2 - b^2 = (a - b)(a + b)" />
      </div>
    </div>
    
    <div className="factorization-rule">
      <h4>3. Trinomio cuadrado perfecto</h4>
      <div className="rule-example">
        <BlockMath math="a^2 + 2ab + b^2 = (a + b)^2" />
        <BlockMath math="a^2 - 2ab + b^2 = (a - b)^2" />
      </div>
    </div>
    
    <div className="factorization-rule">
      <h4>4. Trinomio de la forma x² + bx + c</h4>
      <p>Buscamos dos números m,n tales que m + n = b y m · n = c, luego:</p>
      <div className="rule-example">
        <BlockMath math="x^2 + bx + c = (x + m)(x + n)" />
      </div>
    </div>
    
    <div className="factorization-rule">
      <h4>5. Suma o diferencia de cubos</h4>
      <div className="rule-example">
        <BlockMath math="a^3 + b^3 = (a + b)(a^2 - ab + b^2)" />
        <BlockMath math="a^3 - b^3 = (a - b)(a^2 + ab + b^2)" />
      </div>
    </div>
  </div>
);

const ExamplesSection: React.FC = () => (
  <div className="tutorial-page">
    <h3 className="tutorial-section-heading">Ejemplos resueltos</h3>
    
    <div className="factorization-example">
      <h4>Ejemplo 1: Factor común</h4>
      <p>Factoriza <InlineMath math="6x^2y - 9x^2y" /></p>
      <div className="example-steps">
        <p>1. MCD de coeficientes: <InlineMath math="3x^2y" /></p>
        <p>2. Extraemos:</p>
        <BlockMath math="6x^2y - 9x^2y = 3x^2y(2 - 3)" />
        <BlockMath math="= 3x^2y(-1)" />
        <BlockMath math="= -3x^2y" />
      </div>
    </div>
    
    <div className="factorization-example">
      <h4>Ejemplo 2: Diferencia de cuadrados</h4>
      <p>Factoriza <InlineMath math="x^2 - 9" /></p>
      <div className="example-steps">
        <BlockMath math="x^2 - 9 = x^2 - 3^2 = (x - 3)(x + 3)" />
      </div>
    </div>
    
    <div className="factorization-example">
      <h4>Ejemplo 3: Trinomio cuadrado perfecto</h4>
      <p>Factoriza <InlineMath math="x^2 + 6x + 9" /></p>
      <div className="example-steps">
        <p>Observa que <InlineMath math="6x = 2 \cdot 3 \cdot x" /> y <InlineMath math="9 = 3^2" />, luego:</p>
        <BlockMath math="x^2 + 6x + 9 = (x + 3)^2" />
      </div>
    </div>
  </div>
);

const BusinessApplicationsSection: React.FC = () => (
  <div className="tutorial-page">
    <h3 className="tutorial-section-heading">Ejercicios aplicados a negocios y marketing</h3>
    
    <div className="business-application">
      <h4>4.1. Función de beneficio y punto de equilibrio</h4>
      <div className="application-problem">
        <p>Supón que:</p>
        <ul>
          <li>
            <strong>Ingreso:</strong> <InlineMath math="R(x) = 50x - x^2" /> 
            <span className="hint">(precio por unidad 50, demanda cuadrática)</span>
          </li>
          <li>
            <strong>Costo:</strong> <InlineMath math="C(x) = 10x + 200" />
          </li>
        </ul>
        
        <p>Entonces el beneficio es:</p>
        <BlockMath math="P(x) = R(x) - C(x) = (50x - x^2) - (10x + 200) = -x^2 + 40x - 200" />
        
        <p>Factoriza <InlineMath math="P(x)" /> para hallar las raíces (puntos donde <InlineMath math="P(x) = 0" />):</p>
        
        <ol className="solution-steps">
          <li>Multiplicamos por -1:
            <BlockMath math="-P(x) = x^2 - 40x + 200" />
          </li>
          <li>Completando el cuadrado:
            <BlockMath math="x^2 - 40x + 200 = (x - 20)^2 - 400 + 200 = (x - 20)^2 - 200" />
          </li>
          <li>Luego:
            <BlockMath math="P(x) = -[(x - 20)^2 - 200] = -(x - 20)^2 + 200" />
          </li>
          <li>Para <InlineMath math="P(x) = 0" />:
            <BlockMath math="-(x - 20)^2 + 200 = 0" />
            <BlockMath math="(x - 20)^2 = 200" />
            <BlockMath math="x - 20 = \pm \sqrt{200} = \pm 14.14" />
            <BlockMath math="x = 20 \pm 14.14" />
          </li>
        </ol>
        
        <p>Por tanto, los puntos de equilibrio son aproximadamente x = 5.86 y x = 34.14.</p>
      </div>
    </div>
  </div>
);

const ExercisesSection: React.FC = () => (
  <div className="tutorial-page">
    <h3 className="tutorial-section-heading">Ejercicios propuestos</h3>
    
    <div className="practice-exercises">
      <div className="exercise-category">
        <h4>1. Factor común</h4>
        <ul>
          <li><InlineMath math="12x^2 - 18x" /></li>
          <li><InlineMath math="7ab^2 - 14a^2b" /></li>
        </ul>
      </div>
      
      <div className="exercise-category">
        <h4>2. Diferencia de cuadrados</h4>
        <ul>
          <li><InlineMath math="9y^2 - 16" /></li>
          <li><InlineMath math="4x^4 - 1" /></li>
        </ul>
      </div>
      
      <div className="exercise-category">
        <h4>3. Trinomio general</h4>
        <ul>
          <li><InlineMath math="x^2 + 11x + 28" /></li>
          <li><InlineMath math="t^2 - 5t - 14" /></li>
        </ul>
      </div>
      
      <div className="exercise-category">
        <h4>4. Suma/diferencia de cubos</h4>
        <ul>
          <li><InlineMath math="8a^3 - 27" /></li>
          <li><InlineMath math="125 + y^3" /></li>
        </ul>
      </div>
      
      <div className="exercise-category">
        <h4>5. Aplicado a finanzas</h4>
        <p>
          Una empresa tiene:
          <BlockMath math="R(x) = 100x - x^2, \quad C(x) = 20x + 300" />
        </p>
        <ol>
          <li>Factoriza <InlineMath math="P(x) = R(x) - C(x)" /> y encuentra las raíces.</li>
          <li>El costo medio de producción es:
            <BlockMath math="c_m(x) = \frac{4x^2 + 40x + 100}{x}" />
            <p>Simplifica la expresión mediante factorización.</p>
          </li>
        </ol>
      </div>
    </div>
  </div>
);

const FactorizationTutorial: React.FC = () => {
  const [currentPage, setCurrentPage] = useState<number>(0);

  // Array de componentes de páginas
  const tutorialPages = [
    <IntroductionSection key="intro" />,
    <BasicRulesSection key="rules" />,
    <ExamplesSection key="examples" />,
    <BusinessApplicationsSection key="business" />,
    <ExercisesSection key="exercises" />
  ];

  const goToPreviousPage = () => {
    setCurrentPage(prev => Math.max(prev - 1, 0));
  };

  const goToNextPage = () => {
    setCurrentPage(prev => Math.min(prev + 1, tutorialPages.length - 1));
  };

  return (
    <div className="factorization-tutorial-container">
      <h2 className="tutorial-title">Tutorial de Factorización y Aplicación a Finanzas</h2>
      
      <div className="tutorial-content">
        <div className="tutorial-page-container">
          {tutorialPages[currentPage]}
        </div>
        
        <div className="tutorial-navigation">
          <button 
            className="tutorial-nav-button"
            onClick={goToPreviousPage}
            disabled={currentPage === 0}
          >
            Anterior
          </button>
          
          <span className="tutorial-page-info">
            Página {currentPage + 1} de {tutorialPages.length}
          </span>
          
          <button 
            className="tutorial-nav-button"
            onClick={goToNextPage}
            disabled={currentPage === tutorialPages.length - 1}
          >
            Siguiente
          </button>
        </div>
      </div>
      
      <div className="tutorial-sections">
        <div className="tutorial-section-list">
          <h3>Contenido del Tutorial:</h3>
          <ul>
            <li 
              className={currentPage === 0 ? 'active' : ''}
              onClick={() => setCurrentPage(0)}
            >
              1. ¿Qué es la factorización?
            </li>
            <li 
              className={currentPage === 1 ? 'active' : ''}
              onClick={() => setCurrentPage(1)}
            >
              2. Reglas básicas de factorización
            </li>
            <li 
              className={currentPage === 2 ? 'active' : ''}
              onClick={() => setCurrentPage(2)}
            >
              3. Ejemplos resueltos
            </li>
            <li 
              className={currentPage === 3 ? 'active' : ''}
              onClick={() => setCurrentPage(3)}
            >
              4. Ejercicios aplicados a negocios
            </li>
            <li 
              className={currentPage === 4 ? 'active' : ''}
              onClick={() => setCurrentPage(4)}
            >
              5. Ejercicios propuestos
            </li>
          </ul>
        </div>
        
        <div className="tutorial-description">
          <h3>Descripción:</h3>
          {currentPage === 0 && (
            <p>
              La factorización consiste en descomponer un polinomio en un producto
              de factores más simples. En finanzas y economía, se usa para:
              analizar funciones de costo, ingreso y beneficio, encontrar puntos de equilibrio,
              y simplificar cálculos en modelado de escenarios.
            </p>
          )}
          {currentPage === 1 && (
            <p>
              Aprende las reglas básicas de factorización: factor común, diferencia de cuadrados,
              trinomio cuadrado perfecto, trinomio de la forma x² + bx + c, y suma o diferencia de cubos.
              Estos métodos son fundamentales para resolver problemas financieros.
            </p>
          )}
          {currentPage === 2 && (
            <p>
              Ejemplos detallados de factorización resueltos paso a paso:
              factor común, diferencia de cuadrados, trinomio cuadrado perfecto,
              trinomio general, y suma de cubos.
            </p>
          )}
          {currentPage === 3 && (
            <p>
              Aplicaciones prácticas a finanzas y negocios: cómo utilizar la factorización
              para encontrar puntos de equilibrio en modelos financieros, analizar funciones
              de beneficio y calcular costos unitarios.
            </p>
          )}
          {currentPage === 4 && (
            <p>
              Ejercicios propuestos para practicar los conceptos aprendidos.
              Incluye problemas de factorización básica y aplicaciones a
              modelos financieros y de marketing.
            </p>
          )}
        </div>
      </div>
    </div>
  );
};

export default FactorizationTutorial; 