import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import './ForgotPassword.css';

const ForgotPassword: React.FC = () => {
  const [email, setEmail] = useState('');
  const [error, setError] = useState('');
  const [message, setMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const [showInstructions, setShowInstructions] = useState(false);
  const [resetToken, setResetToken] = useState('');
  const [username, setUsername] = useState('');

  const { requestPasswordReset } = useAuth();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!email || !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
      setError('Por favor ingrese un correo electrónico válido');
      return;
    }

    try {
      setError('');
      setMessage('');
      setLoading(true);
      
      const result = await requestPasswordReset(email);
      
      if (result.success) {
        // En una aplicación real, simplemente mostraríamos un mensaje de éxito
        // Para esta demo, simulamos que se envió un correo y mostramos el token y username
        setShowInstructions(true);
        
        if (result.token) {
          setResetToken(result.token);
        }
        
        // Extraer el nombre de usuario del mensaje de simulación
        const usernameMatch = result.error?.match(/Su nombre de usuario es: (\w+)/);
        if (usernameMatch && usernameMatch[1]) {
          setUsername(usernameMatch[1]);
        }
        
        setMessage('Se ha enviado un enlace de recuperación a su correo electrónico.');
      } else {
        setError(result.error || 'Error al procesar la solicitud');
      }
    } catch (err) {
      setError('Ocurrió un error inesperado');
      console.error('Error en recuperación de contraseña:', err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="forgot-password-container">
      <div className="forgot-password-card">
        <h2>Recuperar Contraseña</h2>
        <p>Ingrese su correo electrónico para recibir instrucciones de recuperación</p>
        
        {error && <div className="error-message">{error}</div>}
        {message && <div className="success-message">{message}</div>}
        
        {!showInstructions ? (
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="email">Correo Electrónico</label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="Ingrese su correo electrónico"
                disabled={loading}
              />
            </div>
            
            <div className="form-group">
              <button 
                type="submit" 
                className="forgot-password-button"
                disabled={loading}
              >
                {loading ? 'Enviando...' : 'Enviar Instrucciones'}
              </button>
            </div>
          </form>
        ) : (
          <div className="recovery-instructions">
            <h3>Simulación de Recuperación</h3>
            <p>En una aplicación real, recibirías un correo con un enlace para restablecer tu contraseña.</p>
            
            {username && (
              <div className="instruction-item">
                <strong>Tu nombre de usuario es:</strong> {username}
              </div>
            )}
            
            {resetToken && (
              <div className="instruction-item">
                <strong>Enlace de recuperación:</strong>
                <Link to={`/reset-password/${resetToken}`} className="recovery-link">
                  Haz clic aquí para restablecer tu contraseña
                </Link>
              </div>
            )}
          </div>
        )}
        
        <div className="return-to-login">
          <Link to="/login">Volver a Inicio de Sesión</Link>
        </div>
      </div>
    </div>
  );
};

export default ForgotPassword; 