import React from 'react';
import './Header.css';
import { useAuth } from '../contexts/AuthContext';
import { useNavigate, Link } from 'react-router-dom';

const Header: React.FC = () => {
  const { user, logout } = useAuth();
  const navigate = useNavigate();

  const handleAuthAction = () => {
    if (user) {
      logout();
      navigate('/login');
    } else {
      navigate('/login');
    }
  };

  return (
    <header className="header">
      <div className="header-container">
        <div className="header-logo-section">
          <Link to="/" className="header-logo">
            <span className="header-logo-text">Matemática Básica</span>
          </Link>
          
          <div className="header-nav">
            <Link to="/" className="nav-link">Inicio</Link>
            {user && (
              <>
                <Link to="/profile" className="nav-link">Perfil</Link>
                <Link to="/documents" className="nav-link">Documentos</Link>
                <Link to="/factorization" className="nav-link">Factorización</Link>
                <Link to="/leaderboard" className="nav-link">Tabla de Posiciones</Link>
                <Link to="/store" className="nav-link">Tienda</Link>
                <Link to="/blockchain" className="nav-link">OroCoins</Link>
              </>
            )}
          </div>
        </div>
        
        <div className="header-user-section">
          {user && (
            <div className="header-user-info">
              <span className="user-name">{user.name || user.username || user.cedula}</span>
            </div>
          )}
          <button 
            className={user ? "logout-button" : "login-button"} 
            onClick={handleAuthAction}
          >
            {user ? 'Cerrar Sesión' : 'Iniciar Sesión'}
          </button>
        </div>
      </div>
    </header>
  );
};

export default Header; 