import React, { useState, useEffect } from 'react';
import PDFUploader from './PDFUploader';
import PDFViewer from './PDFViewer';
import { useAuth } from '../contexts/AuthContext';
import './PDFSection.css';

// Función para guardar PDFs en localStorage
const savePDF = (pdfName: string, pdfUrl: string, userId: string) => {
  try {
    const pdfsKey = `user_pdfs_${userId}`;
    const storedPDFs = localStorage.getItem(pdfsKey);
    const pdfs = storedPDFs ? JSON.parse(storedPDFs) : [];
    
    // Añadir nuevo PDF
    pdfs.push({
      id: `pdf_${Date.now()}`,
      name: pdfName,
      url: pdfUrl,
      date: new Date().toISOString()
    });
    
    // Guardar en localStorage
    localStorage.setItem(pdfsKey, JSON.stringify(pdfs));
    
    return true;
  } catch (error) {
    console.error("Error guardando PDF:", error);
    return false;
  }
};

// Función para cargar los PDFs desde localStorage
const loadPDFs = (userId: string) => {
  try {
    const pdfsKey = `user_pdfs_${userId}`;
    const storedPDFs = localStorage.getItem(pdfsKey);
    return storedPDFs ? JSON.parse(storedPDFs) : [];
  } catch (error) {
    console.error("Error cargando PDFs:", error);
    return [];
  }
};

interface PDF {
  id: string;
  name: string;
  url: string;
  date: string;
}

const PDFSection: React.FC = () => {
  const { user } = useAuth();
  const [uploadedPDFs, setUploadedPDFs] = useState<PDF[]>([]);
  const [selectedPDF, setSelectedPDF] = useState<PDF | null>(null);
  const [showUploader, setShowUploader] = useState<boolean>(false);
  
  // Cargar PDFs al inicio
  useEffect(() => {
    if (user) {
      const pdfs = loadPDFs(user.cedula);
      setUploadedPDFs(pdfs);
    }
  }, [user]);
  
  // Manejar la carga de un nuevo PDF
  const handleFileUpload = (file: File) => {
    if (!user) return;
    
    // Crear una URL para el archivo
    const fileURL = URL.createObjectURL(file);
    const fileName = file.name;
    
    // Guardar el PDF en localStorage
    const saved = savePDF(fileName, fileURL, user.cedula);
    
    if (saved) {
      // Actualizar el estado con el nuevo PDF
      const newPDF = {
        id: `pdf_${Date.now()}`,
        name: fileName,
        url: fileURL,
        date: new Date().toISOString()
      };
      
      setUploadedPDFs(prev => [...prev, newPDF]);
      setSelectedPDF(newPDF);
      setShowUploader(false);
    }
  };
  
  // Formatear fecha
  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('es-ES', { 
      year: 'numeric', 
      month: 'short', 
      day: 'numeric' 
    });
  };
  
  // Eliminar un PDF
  const handleDeletePDF = (pdfId: string) => {
    if (!user) return;
    
    try {
      // Filtrar los PDFs para eliminar el seleccionado
      const updatedPDFs = uploadedPDFs.filter(pdf => pdf.id !== pdfId);
      
      // Actualizar localStorage
      localStorage.setItem(`user_pdfs_${user.cedula}`, JSON.stringify(updatedPDFs));
      
      // Actualizar estado
      setUploadedPDFs(updatedPDFs);
      
      // Si el PDF seleccionado es el que estamos eliminando, deseleccionar
      if (selectedPDF && selectedPDF.id === pdfId) {
        setSelectedPDF(null);
      }
    } catch (error) {
      console.error("Error eliminando PDF:", error);
    }
  };
  
  if (!user) {
    return (
      <div className="pdf-section-container">
        <p className="pdf-section-empty">
          Debes iniciar sesión para acceder a la sección de documentos.
        </p>
      </div>
    );
  }
  
  return (
    <div className="pdf-section-container">
      <div className="pdf-section-header">
        <h2 className="pdf-section-title">Mis Documentos PDF</h2>
        <button 
          className="pdf-section-button"
          onClick={() => setShowUploader(!showUploader)}
        >
          {showUploader ? 'Cancelar' : 'Cargar nuevo PDF'}
        </button>
      </div>
      
      {showUploader && (
        <div className="pdf-section-uploader">
          <PDFUploader onFileUpload={handleFileUpload} />
        </div>
      )}
      
      <div className="pdf-section-content">
        <div className="pdf-section-list">
          <h3 className="pdf-section-subtitle">Mis PDFs</h3>
          
          {uploadedPDFs.length === 0 ? (
            <p className="pdf-section-empty">
              No tienes documentos PDF guardados. ¡Carga uno nuevo!
            </p>
          ) : (
            <ul className="pdf-file-list">
              {uploadedPDFs.map(pdf => (
                <li 
                  key={pdf.id} 
                  className={`pdf-file-item ${selectedPDF?.id === pdf.id ? 'pdf-file-item-selected' : ''}`}
                  onClick={() => setSelectedPDF(pdf)}
                >
                  <div className="pdf-file-icon">📄</div>
                  <div className="pdf-file-info">
                    <div className="pdf-file-name">{pdf.name}</div>
                    <div className="pdf-file-date">{formatDate(pdf.date)}</div>
                  </div>
                  <button
                    className="pdf-file-delete"
                    onClick={(e) => {
                      e.stopPropagation();
                      handleDeletePDF(pdf.id);
                    }}
                  >
                    🗑️
                  </button>
                </li>
              ))}
            </ul>
          )}
        </div>
        
        <div className="pdf-section-viewer">
          {selectedPDF ? (
            <PDFViewer file={selectedPDF.url} title={selectedPDF.name} />
          ) : (
            <div className="pdf-viewer-placeholder">
              <p>
                Selecciona un PDF de la lista para visualizarlo o carga uno nuevo.
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default PDFSection; 