import React, { useState, useEffect } from 'react';
import './App.css';
import Header from './components/Header';
import Footer from './components/Footer';
import ExerciseDisplay from './components/ExerciseDisplay';
import ExerciseGenerator from './utils/ExerciseGenerator';
import { useAuth } from './contexts/AuthContext';
import { useNavigate } from 'react-router-dom';

function App() {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [exerciseType, setExerciseType] = useState<'notables' | 'operations'>('notables');
  const [difficulty, setDifficulty] = useState<'easy' | 'medium' | 'hard'>('easy');
  const [currentExercise, setCurrentExercise] = useState<string>('');
  const [solution, setSolution] = useState<string>('');
  const [showSolution, setShowSolution] = useState<boolean>(false);
  const [userAnswer, setUserAnswer] = useState<string>('');
  const [isCorrect, setIsCorrect] = useState<boolean | null>(null);
  const [usePredefined, setUsePredefined] = useState<boolean>(false);
  const [predefinedExercises, setPredefinedExercises] = useState<{exercise: string, solution: string}[]>([]);
  const [currentExerciseIndex, setCurrentExerciseIndex] = useState<number>(0);
  const [isVerified, setIsVerified] = useState<boolean>(false);
  const [coins, setCoins] = useState<number>(0);
  const [showStorePromo, setShowStorePromo] = useState<boolean>(false);

  // Cargar las monedas del usuario desde localStorage
  useEffect(() => {
    if (user) {
      const userCoins = localStorage.getItem(`user_coins_${user.cedula}`);
      if (userCoins) {
        setCoins(parseInt(userCoins, 10));
      }
    }
  }, [user]);

  // Guardar las monedas en localStorage cuando cambien
  useEffect(() => {
    if (user && coins > 0) {
      localStorage.setItem(`user_coins_${user.cedula}`, coins.toString());
    }
  }, [coins, user]);

  useEffect(() => {
    // Cargar ejercicios predefinidos al cambiar el tipo
    if (usePredefined) {
      const exercises = ExerciseGenerator.getPredefinedExercises(exerciseType);
      setPredefinedExercises(exercises);
      setCurrentExerciseIndex(0);
      
      if (exercises.length > 0) {
        setCurrentExercise(exercises[0].exercise);
        setSolution(exercises[0].solution);
      }
    }
  }, [exerciseType, usePredefined]);

  const generateNewExercise = () => {
    setShowSolution(false);
    setUserAnswer('');
    setIsCorrect(null);
    setIsVerified(false);
    setShowStorePromo(false);
    
    if (usePredefined) {
      // Avanzar al siguiente ejercicio predefinido
      const nextIndex = (currentExerciseIndex + 1) % predefinedExercises.length;
      setCurrentExerciseIndex(nextIndex);
      setCurrentExercise(predefinedExercises[nextIndex].exercise);
      setSolution(predefinedExercises[nextIndex].solution);
    } else {
      // Generar ejercicio aleatorio
      const { exercise, solution } = ExerciseGenerator.generate(exerciseType, difficulty);
      setCurrentExercise(exercise);
      setSolution(solution);
    }
  };

  // Obtener el valor de monedas basado en la dificultad
  const getCoinsValue = (): number => {
    switch (difficulty) {
      case 'easy':
        return 1;
      case 'medium':
        return 2;
      case 'hard':
        return 3;
      default:
        return 1;
    }
  };

  // Normalizar respuestas algebraicas
  const normalizeAnswer = (answer: string): string => {
    let normalized = answer.replace(/\s+/g, ''); // Eliminar espacios
    
    // Reemplazar términos como "b^2" con "1b^2" para normalizar coeficientes implícitos
    normalized = normalized.replace(/^([a-zA-Z]\^[0-9]+)/g, '1$1'); // Al inicio de la expresión
    normalized = normalized.replace(/([+\-])([a-zA-Z]\^[0-9]+)/g, '$11$2'); // Después de + o -
    
    // También normalizar términos como "b" a "1b"
    normalized = normalized.replace(/^([a-zA-Z])(?!\^)/g, '1$1'); // Al inicio
    normalized = normalized.replace(/([+\-])([a-zA-Z])(?!\^)/g, '$11$2'); // Después de + o -
    
    return normalized;
  };

  const saveExerciseToHistory = (coinsEarned: number, isCorrect: boolean) => {
    if (!user) return;
    
    const exerciseId = `ex_${Date.now()}`;
    const exerciseData = {
      id: exerciseId,
      type: exerciseType,
      difficulty,
      date: new Date().toISOString(),
      coins: isCorrect ? coinsEarned : 0,
      correct: isCorrect
    };
    
    // Obtener historial actual
    const historyKey = `exercise_history_${user.cedula}`;
    const currentHistory = localStorage.getItem(historyKey);
    const history = currentHistory ? JSON.parse(currentHistory) : [];
    
    // Agregar nuevo ejercicio
    history.push(exerciseData);
    
    // Guardar historial actualizado
    localStorage.setItem(historyKey, JSON.stringify(history));
  };

  const checkAnswer = () => {
    // Si ya está verificado, no hacer nada
    if (isVerified) return;
    
    const userAnswerNormalized = normalizeAnswer(userAnswer);
    const solutionNormalized = normalizeAnswer(solution);
    
    console.log('User answer normalized:', userAnswerNormalized);
    console.log('Solution normalized:', solutionNormalized);
    
    const correct = userAnswerNormalized === solutionNormalized;
    setIsCorrect(correct);
    setIsVerified(true);
    
    // Si la respuesta es correcta, otorgar monedas
    const coinsEarned = getCoinsValue();
    
    if (correct && user) {
      setCoins(prevCoins => prevCoins + coinsEarned);
      
      // Mostrar promoción de la tienda cada 3 ejercicios correctos aproximadamente
      if (Math.random() < 0.3) {
        setShowStorePromo(true);
      }
    }
    
    // Guardar en historial
    saveExerciseToHistory(coinsEarned, correct);
  };

  const revealSolution = () => {
    // Solo permitir mostrar la solución si ya se verificó la respuesta
    if (isVerified) {
      setShowSolution(true);
    }
  };

  const toggleExerciseMode = () => {
    setUsePredefined(!usePredefined);
    setShowSolution(false);
    setUserAnswer('');
    setIsCorrect(null);
    setIsVerified(false);
    setShowStorePromo(false);
    
    if (!usePredefined) {
      // Cambiar a ejercicios predefinidos
      const exercises = ExerciseGenerator.getPredefinedExercises(exerciseType);
      setPredefinedExercises(exercises);
      setCurrentExerciseIndex(0);
      
      if (exercises.length > 0) {
        setCurrentExercise(exercises[0].exercise);
        setSolution(exercises[0].solution);
      }
    } else {
      // Volver a ejercicios aleatorios
      setCurrentExercise('');
      setSolution('');
    }
  };

  const handleGoToStore = () => {
    navigate('/store');
  };

  return (
    <div className="app-container">
      <Header />
      
      <main className="main-content">
        <div className="exercise-card">
          <h2 className="title">
            Ejercicios de Matemática Básica
          </h2>
          
          {user && (
            <div className="user-welcome">
              Bienvenido, {user.name || user.cedula}
              <div className="user-coins">
                <span role="img" aria-label="moneda">🪙</span> {coins} monedas
              </div>
            </div>
          )}
          
          <div className="form-section">
            <div className="exercise-mode-toggle">
              <button
                className={`mode-button ${usePredefined ? 'mode-button-active' : ''}`}
                onClick={toggleExerciseMode}
              >
                {usePredefined ? 'Usando Hoja de Ejercicios' : 'Usar Hoja de Ejercicios'}
              </button>
            </div>
            
            <div className="form-row">
              <div className="form-column">
                <label className="form-label">
                  Tipo de Ejercicio
                </label>
                <select 
                  className="form-select"
                  value={exerciseType}
                  onChange={(e) => setExerciseType(e.target.value as 'notables' | 'operations')}
                >
                  <option value="notables">Productos Notables</option>
                  <option value="operations">Orden de las Operaciones</option>
                </select>
              </div>
              
              {!usePredefined && (
                <div className="form-column">
                  <label className="form-label">
                    Dificultad
                  </label>
                  <select 
                    className="form-select"
                    value={difficulty}
                    onChange={(e) => setDifficulty(e.target.value as 'easy' | 'medium' | 'hard')}
                  >
                    <option value="easy">Fácil (1 moneda)</option>
                    <option value="medium">Medio (2 monedas)</option>
                    <option value="hard">Difícil (3 monedas)</option>
                  </select>
                </div>
              )}
            </div>
            
            <button 
              className="generate-button"
              onClick={generateNewExercise}
            >
              {isVerified 
                ? 'Generar Nuevo Ejercicio' 
                : (usePredefined ? 'Siguiente Ejercicio' : 'Generar Nuevo Ejercicio')}
            </button>
          </div>
          
          {currentExercise && (
            <ExerciseDisplay 
              exercise={currentExercise}
              solution={solution}
              showSolution={showSolution}
              userAnswer={userAnswer}
              setUserAnswer={setUserAnswer}
              isCorrect={isCorrect}
              isVerified={isVerified}
              onCheck={checkAnswer}
              onReveal={revealSolution}
            />
          )}
          
          {!currentExercise && (
            <div className="empty-state">
              <p className="empty-state-text">
                Haz clic en {usePredefined ? '"Siguiente Ejercicio"' : '"Generar Nuevo Ejercicio"'} para comenzar.
              </p>
            </div>
          )}

          {isCorrect && isVerified && !showSolution && (
            <div className="coins-earned">
              <p>¡Has ganado {getCoinsValue()} {getCoinsValue() === 1 ? 'moneda' : 'monedas'}!</p>
            </div>
          )}

          {showStorePromo && (
            <div className="store-promo">
              <h3>¡Gasta tus monedas en la Tienda!</h3>
              <p>Ya tienes {coins} monedas. Puedes canjearlas por premios en la tienda virtual.</p>
              <button 
                className="store-promo-button"
                onClick={handleGoToStore}
              >
                Ir a la Tienda <span className="arrow-icon">→</span>
              </button>
            </div>
          )}
        </div>
      </main>
      
      <Footer />
    </div>
  );
}

export default App;
