import React from 'react';
import { InlineMath, BlockMath } from '../utils/MathRenderer';
import './ExerciseDisplay.css';

interface ExerciseDisplayProps {
  exercise: string;
  solution: string;
  showSolution: boolean;
  userAnswer: string;
  setUserAnswer: (answer: string) => void;
  isCorrect: boolean | null;
  isVerified: boolean;
  onCheck: () => void;
  onReveal: () => void;
}

const ExerciseDisplay: React.FC<ExerciseDisplayProps> = ({
  exercise,
  solution,
  showSolution,
  userAnswer,
  setUserAnswer,
  isCorrect,
  isVerified,
  onCheck,
  onReveal
}) => {
  return (
    <div className="exercise-container">
      <div className="exercise-section">
        <h3 className="exercise-title">Ejercicio:</h3>
        <div className="exercise-display">
          <BlockMath math={exercise} />
        </div>
      </div>
      
      <div className="exercise-section">
        <label htmlFor="answer" className="answer-label">
          Tu Respuesta:
        </label>
        <input
          type="text"
          id="answer"
          className={`answer-input ${
            isCorrect === null 
              ? 'answer-input-default' 
              : isCorrect 
                ? 'answer-input-correct' 
                : 'answer-input-incorrect'
          }`}
          value={userAnswer}
          onChange={(e) => setUserAnswer(e.target.value)}
          placeholder="Escribe tu respuesta aquí"
          disabled={isVerified}
        />
        
        <div className="input-help">
          <p>Formato de respuesta:</p>
          <ul>
            <li>Para exponentes usa ^ (ejemplo: x^2 para x²)</li>
            <li>Para raíces cuadradas usa sqrt() (ejemplo: sqrt(x) para √x)</li>
            <li>Para fracciones usa / (ejemplo: 1/2 para ½)</li>
            <li>No uses espacios entre términos</li>
            <li>Para expresiones algebraicas como b^2+6b+9, puedes omitir el 1 delante de b^2</li>
          </ul>
        </div>
        
        {isCorrect !== null && (
          <p className={`feedback-message ${isCorrect ? 'feedback-message-correct' : 'feedback-message-incorrect'}`}>
            {isCorrect 
              ? '¡Correcto! ¡Muy bien!' 
              : 'Incorrecto. Inténtalo de nuevo o revisa la solución.'}
          </p>
        )}
      </div>
      
      <div className="button-container">
        <button
          className="check-button"
          onClick={onCheck}
          disabled={isVerified || !userAnswer.trim()}
        >
          Verificar Respuesta
        </button>
        
        <button
          className={`solution-button ${!isVerified || showSolution ? 'solution-button-disabled' : ''}`}
          onClick={onReveal}
          disabled={!isVerified || showSolution}
        >
          {showSolution ? 'Solución Mostrada' : 'Mostrar Solución'}
        </button>
      </div>
      
      {showSolution && (
        <div className="solution-container">
          <h4 className="solution-title">Solución:</h4>
          <div className="solution-display">
            <BlockMath math={solution} />
          </div>
          <div className="solution-note">
            <p>
              Recuerda practicar este tipo de ejercicios para mejorar tu comprensión de los conceptos matemáticos.
            </p>
          </div>
        </div>
      )}
      
      {isVerified && !isCorrect && !showSolution && (
        <div className="next-step-hint">
          <p>Para ver la solución correcta, haz clic en "Mostrar Solución" o genera un nuevo ejercicio.</p>
        </div>
      )}
      
      {isVerified && isCorrect && !showSolution && (
        <div className="next-step-hint correct-hint">
          <p>¡Excelente! Puedes continuar con un nuevo ejercicio para ganar más monedas.</p>
        </div>
      )}
    </div>
  );
};

export default ExerciseDisplay; 