import React, { useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

interface ProtectedRouteProps {
  children: React.ReactNode;
}

const ProtectedRoute: React.FC<ProtectedRouteProps> = ({ children }) => {
  const { user, loading } = useAuth();

  if (loading) {
    // Muestra un indicador de carga mientras se verifica el estado de autenticación
    return <div className="loading">Cargando...</div>;
  }

  if (!user) {
    // Redirige a la página de login si no hay usuario autenticado
    return <Navigate to="/login" />;
  }

  // Si el usuario no ha completado su registro, redirigir a la página de completar registro
  if (!user.registrationComplete) {
    return <Navigate to="/complete-registration" />;
  }

  // Si el usuario está autenticado y completó su registro, mostrar el componente hijo
  return <>{children}</>;
};

export default ProtectedRoute; 