import React, { useState, useEffect } from 'react';
import { useParams, useNavigate, Link } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import './ResetPassword.css';

const ResetPassword: React.FC = () => {
  const { token } = useParams<{ token: string }>();
  const navigate = useNavigate();
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [message, setMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);

  const { resetPassword } = useAuth();

  // Validar que existe un token
  useEffect(() => {
    if (!token) {
      navigate('/forgot-password');
    }
  }, [token, navigate]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // Validaciones
    if (newPassword.length < 6) {
      setError('La contraseña debe tener al menos 6 caracteres');
      return;
    }

    if (newPassword !== confirmPassword) {
      setError('Las contraseñas no coinciden');
      return;
    }

    try {
      setError('');
      setMessage('');
      setLoading(true);
      
      if (!token) {
        setError('Token de recuperación inválido');
        return;
      }
      
      const result = await resetPassword(token, newPassword);
      
      if (result.success) {
        setIsSuccess(true);
        setMessage('Contraseña restablecida correctamente. Ahora puede iniciar sesión con su nueva contraseña.');
      } else {
        setError(result.error || 'Error al restablecer la contraseña');
      }
    } catch (err) {
      setError('Ocurrió un error inesperado');
      console.error('Error al restablecer contraseña:', err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="reset-password-container">
      <div className="reset-password-card">
        <h2>Restablecer Contraseña</h2>
        
        {isSuccess ? (
          <div className="reset-success">
            <div className="success-message">{message}</div>
            <Link to="/login" className="login-link">Ir a Inicio de Sesión</Link>
          </div>
        ) : (
          <>
            <p>Ingrese su nueva contraseña</p>
            
            {error && <div className="error-message">{error}</div>}
            
            <form onSubmit={handleSubmit}>
              <div className="form-group">
                <label htmlFor="newPassword">Nueva Contraseña</label>
                <input
                  type="password"
                  id="newPassword"
                  value={newPassword}
                  onChange={(e) => setNewPassword(e.target.value)}
                  placeholder="Mínimo 6 caracteres"
                  disabled={loading}
                />
              </div>
              
              <div className="form-group">
                <label htmlFor="confirmPassword">Confirmar Contraseña</label>
                <input
                  type="password"
                  id="confirmPassword"
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  placeholder="Repita la contraseña"
                  disabled={loading}
                />
              </div>
              
              <div className="form-group">
                <button 
                  type="submit" 
                  className="reset-password-button"
                  disabled={loading}
                >
                  {loading ? 'Procesando...' : 'Restablecer Contraseña'}
                </button>
              </div>
            </form>
            
            <div className="return-to-login">
              <Link to="/login">Cancelar y volver a Inicio de Sesión</Link>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default ResetPassword; 