import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import './CompleteRegistration.css';

const CompleteRegistration: React.FC = () => {
  const [name, setName] = useState('');
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const { user, completeRegistration } = useAuth();
  const navigate = useNavigate();

  // Inicializar nombre de usuario con la cédula
  useEffect(() => {
    if (user && user.cedula) {
      setUsername(user.cedula);
    }
  }, [user]);

  // Redireccionar si el usuario ya completó su registro o no está autenticado
  useEffect(() => {
    if (!user) {
      navigate('/login');
    } else if (user.registrationComplete) {
      navigate('/');
    }
  }, [user, navigate]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // Validaciones
    if (!name.trim()) {
      setError('Por favor ingrese su nombre completo');
      return;
    }

    if (!username.trim() || username.length < 4) {
      setError('Por favor ingrese un nombre de usuario de al menos 4 caracteres');
      return;
    }
    
    if (!email.trim() || !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
      setError('Por favor ingrese un correo electrónico válido');
      return;
    }

    if (newPassword.length < 6) {
      setError('La contraseña debe tener al menos 6 caracteres');
      return;
    }

    if (newPassword !== confirmPassword) {
      setError('Las contraseñas no coinciden');
      return;
    }

    try {
      setError('');
      setLoading(true);
      
      const result = await completeRegistration(name, username, email, newPassword);
      
      if (!result.success) {
        setError(result.error || 'Error al completar el registro');
      }
    } catch (err) {
      setError('Ocurrió un error inesperado');
      console.error('Error al completar registro:', err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="registration-container">
      <div className="registration-card">
        <h2>Complete su Registro</h2>
        <p>Para continuar, por favor complete la siguiente información</p>
        
        {error && <div className="error-message">{error}</div>}
        
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="name">Nombre Completo</label>
            <input
              type="text"
              id="name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="Ingrese su nombre completo"
              disabled={loading}
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="username">Nombre de Usuario</label>
            <input
              type="text"
              id="username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              placeholder="Nombre para iniciar sesión"
              disabled={loading}
            />
            <small className="input-help">Este será su identificador para iniciar sesión</small>
          </div>
          
          <div className="form-group">
            <label htmlFor="email">Correo Electrónico</label>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="ejemplo@correo.com"
              disabled={loading}
            />
            <small className="input-help">Necesario para recuperar su cuenta si olvida su contraseña</small>
          </div>
          
          <div className="form-group">
            <label htmlFor="newPassword">Nueva Contraseña</label>
            <input
              type="password"
              id="newPassword"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              placeholder="Mínimo 6 caracteres"
              disabled={loading}
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="confirmPassword">Confirmar Contraseña</label>
            <input
              type="password"
              id="confirmPassword"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              placeholder="Repita la contraseña"
              disabled={loading}
            />
          </div>
          
          <div className="form-group">
            <button 
              type="submit" 
              className="registration-button"
              disabled={loading}
            >
              {loading ? 'Guardando...' : 'Completar Registro'}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CompleteRegistration; 