import React, { useState } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
import './PDFViewer.css';

// Configuración del worker de PDF.js
pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.js`;

interface PDFViewerProps {
  file: string | File; // URL o archivo de PDF
  title?: string;
}

const PDFViewer: React.FC<PDFViewerProps> = ({ file, title }) => {
  const [numPages, setNumPages] = useState<number | null>(null);
  const [pageNumber, setPageNumber] = useState<number>(1);
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  const onDocumentLoadSuccess = ({ numPages }: { numPages: number }) => {
    setNumPages(numPages);
    setIsLoading(false);
    setError(null);
  };

  const onDocumentLoadError = (error: Error) => {
    setError(`Error al cargar el PDF: ${error.message}`);
    setIsLoading(false);
  };

  const goToPreviousPage = () => {
    setPageNumber(prevPage => Math.max(prevPage - 1, 1));
  };

  const goToNextPage = () => {
    setPageNumber(prevPage => Math.min(prevPage + 1, numPages || 1));
  };

  return (
    <div className="pdf-viewer-container">
      {title && <h3 className="pdf-title">{title}</h3>}
      
      <div className="pdf-document-container">
        {isLoading && <div className="pdf-loading">Cargando PDF...</div>}
        {error && <div className="pdf-error">{error}</div>}
        
        <Document 
          file={file}
          onLoadSuccess={onDocumentLoadSuccess}
          onLoadError={onDocumentLoadError}
          className="pdf-document"
        >
          <Page 
            pageNumber={pageNumber} 
            renderTextLayer={false}
            renderAnnotationLayer={false}
            className="pdf-page"
          />
        </Document>
      </div>
      
      {numPages && (
        <div className="pdf-controls">
          <button 
            onClick={goToPreviousPage} 
            disabled={pageNumber <= 1}
            className="pdf-control-button"
          >
            Anterior
          </button>
          
          <span className="pdf-page-info">
            Página {pageNumber} de {numPages}
          </span>
          
          <button 
            onClick={goToNextPage} 
            disabled={pageNumber >= numPages}
            className="pdf-control-button"
          >
            Siguiente
          </button>
        </div>
      )}
    </div>
  );
};

export default PDFViewer; 