import React, { useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import FactorizationTutorial from './FactorizationTutorial';
import './FactorizationSection.css';

// Imágenes de tutorial (se guardarán en la carpeta public)
const createFactorizationImages = () => {
  // Aquí se cargarían las imágenes del PDF de tutorial
  // Para propósitos de desarrollo, usamos el localStorage para simular la carga
  localStorage.setItem('factorization_tutorial_loaded', 'true');
};

// Tipos de ejercicios de factorización
interface FactorizationExercise {
  id: string;
  type: 'factor_comun' | 'diferencia_cuadrados' | 'trinomio_cuadrado' | 'trinomio_general' | 'suma_diferencia_cubos' | 'aplicado_finanzas';
  expression: string;
  solution: string;
  difficulty: 'easy' | 'medium' | 'hard';
}

const FactorizationSection: React.FC = () => {
  const { user } = useAuth();
  const [activeTab, setActiveTab] = useState<'tutorial' | 'exercises'>('tutorial');
  
  // Asegurarse de que las imágenes del tutorial estén cargadas
  React.useEffect(() => {
    createFactorizationImages();
  }, []);

  if (!user) {
    return (
      <div className="factorization-section-container">
        <p className="factorization-section-empty">
          Debes iniciar sesión para acceder a la sección de factorización.
        </p>
      </div>
    );
  }

  return (
    <div className="factorization-section-container">
      <div className="factorization-section-header">
        <h2 className="factorization-section-title">Factorización y Aplicaciones</h2>
        <div className="factorization-section-tabs">
          <button 
            className={`factorization-tab ${activeTab === 'tutorial' ? 'active' : ''}`}
            onClick={() => setActiveTab('tutorial')}
          >
            Tutorial
          </button>
          <button 
            className={`factorization-tab ${activeTab === 'exercises' ? 'active' : ''}`}
            onClick={() => setActiveTab('exercises')}
          >
            Ejercicios
          </button>
        </div>
      </div>

      <div className="factorization-section-content">
        {activeTab === 'tutorial' ? (
          <FactorizationTutorial />
        ) : (
          <div className="factorization-exercises">
            <h3 className="factorization-exercises-title">Ejercicios de Factorización</h3>
            
            <div className="exercise-types-grid">
              <div className="exercise-type-card">
                <h4>Factor Común</h4>
                <p>Extrae el factor común de expresiones algebraicas.</p>
                <button className="exercise-start-button">Practicar</button>
              </div>
              
              <div className="exercise-type-card">
                <h4>Diferencia de Cuadrados</h4>
                <p>Factoriza expresiones de la forma a² - b².</p>
                <button className="exercise-start-button">Practicar</button>
              </div>
              
              <div className="exercise-type-card">
                <h4>Trinomio Cuadrado Perfecto</h4>
                <p>Identifica y factoriza trinomios cuadrados perfectos.</p>
                <button className="exercise-start-button">Practicar</button>
              </div>
              
              <div className="exercise-type-card">
                <h4>Trinomio General</h4>
                <p>Factoriza trinomios de la forma ax² + bx + c.</p>
                <button className="exercise-start-button">Practicar</button>
              </div>
              
              <div className="exercise-type-card">
                <h4>Suma/Diferencia de Cubos</h4>
                <p>Factoriza expresiones de la forma a³ ± b³.</p>
                <button className="exercise-start-button">Practicar</button>
              </div>
              
              <div className="exercise-type-card">
                <h4>Aplicaciones a Finanzas</h4>
                <p>Resuelve problemas financieros usando factorización.</p>
                <button className="exercise-start-button">Practicar</button>
              </div>
            </div>
            
            <div className="factorization-progress">
              <h3>Tu Progreso</h3>
              <div className="progress-stats">
                <div className="progress-stat">
                  <span className="stat-label">Ejercicios completados:</span>
                  <span className="stat-value">0</span>
                </div>
                <div className="progress-stat">
                  <span className="stat-label">Precisión:</span>
                  <span className="stat-value">0%</span>
                </div>
                <div className="progress-stat">
                  <span className="stat-label">Monedas ganadas:</span>
                  <span className="stat-value">0</span>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default FactorizationSection; 