import React, { useEffect, useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';
import './Blockchain.css';
import * as blockchainService from '../services/blockchainService';

interface BlockchainWallet {
  address: string;
  privateKey: string;
  balance: number;
}

interface BlockchainTransaction {
  id: string;
  from: string;
  to: string;
  amount: number;
  timestamp: number;
  status: 'pending' | 'completed' | 'failed';
  hash: string;
}

interface NFT {
  id: string;
  name: string;
  owner: string;
  metadata: {
    description: string;
    image: string;
    attributes: {
      trait_type: string;
      value: string;
    }[];
  };
  createdAt: number;
}

const Blockchain: React.FC = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [wallet, setWallet] = useState<BlockchainWallet | null>(null);
  const [transactions, setTransactions] = useState<BlockchainTransaction[]>([]);
  const [nfts, setNfts] = useState<NFT[]>([]);
  const [coins, setCoins] = useState<number>(0);
  const [convertAmount, setConvertAmount] = useState<number>(0);
  const [message, setMessage] = useState<{ text: string; type: 'success' | 'error' | 'info' | null }>({ text: '', type: null });
  const [showPrivateKey, setShowPrivateKey] = useState<boolean>(false);
  const [activeTab, setActiveTab] = useState<'wallet' | 'transactions' | 'nfts'>('wallet');

  useEffect(() => {
    if (!user) {
      navigate('/login');
      return;
    }

    // Cargar monedas del usuario
    const userCoins = localStorage.getItem(`user_coins_${user.cedula}`);
    if (userCoins) {
      setCoins(parseInt(userCoins, 10));
    }

    // Cargar o crear wallet
    const userWallet = blockchainService.getWallet(user.cedula);
    if (userWallet) {
      setWallet(userWallet);
    } else {
      const newWallet = blockchainService.createWallet(user.cedula);
      setWallet(newWallet);
    }

    // Cargar transacciones
    if (user.cedula) {
      const userTransactions = blockchainService.getUserTransactions(user.cedula);
      setTransactions(userTransactions);

      // Cargar NFTs
      const userNfts = blockchainService.getUserNFTs(user.cedula);
      setNfts(userNfts);
    }
  }, [user, navigate]);

  const handleConvertCoins = () => {
    if (!user) return;
    
    if (convertAmount <= 0) {
      setMessage({ text: 'Por favor ingresa una cantidad válida', type: 'error' });
      return;
    }
    
    if (coins < convertAmount) {
      setMessage({ text: 'No tienes suficientes monedas para convertir', type: 'error' });
      return;
    }
    
    // Convertir monedas a tokens
    const success = blockchainService.convertCoinsToTokens(user.cedula, convertAmount);
    
    if (success) {
      // Actualizar monedas locales
      const newCoins = coins - convertAmount;
      setCoins(newCoins);
      localStorage.setItem(`user_coins_${user.cedula}`, newCoins.toString());
      
      // Actualizar wallet
      const updatedWallet = blockchainService.getWallet(user.cedula);
      setWallet(updatedWallet);
      
      // Actualizar transacciones
      const userTransactions = blockchainService.getUserTransactions(user.cedula);
      setTransactions(userTransactions);
      
      setMessage({ 
        text: `¡Has convertido ${convertAmount} monedas en OroTokens!`, 
        type: 'success' 
      });
      setConvertAmount(0);
    } else {
      setMessage({ 
        text: 'Error al convertir monedas a tokens', 
        type: 'error' 
      });
    }
  };

  const truncateAddress = (address: string): string => {
    return `${address.substring(0, 6)}...${address.substring(address.length - 4)}`;
  };

  const formatDate = (timestamp: number): string => {
    return new Date(timestamp).toLocaleString();
  };

  const handleGoBack = () => {
    navigate('/'); // Navegar a la página principal
  };

  return (
    <div className="blockchain-container">
      <div className="blockchain-header">
        <div className="header-content">
          <h2>OroCoins Blockchain</h2>
          <button 
            className="back-button"
            onClick={handleGoBack}
          >
            ← Volver a ejercicios
          </button>
        </div>
        <div className="blockchain-coins">
          <span role="img" aria-label="moneda">🪙</span> {coins} monedas
        </div>
      </div>

      {message.text && (
        <div className={`message ${message.type}`}>
          {message.text}
          <button 
            className="message-close"
            onClick={() => setMessage({ text: '', type: null })}
          >
            ×
          </button>
        </div>
      )}

      <div className="blockchain-tabs">
        <button 
          className={`tab-button ${activeTab === 'wallet' ? 'active' : ''}`}
          onClick={() => setActiveTab('wallet')}
        >
          Mi Wallet
        </button>
        <button 
          className={`tab-button ${activeTab === 'transactions' ? 'active' : ''}`}
          onClick={() => setActiveTab('transactions')}
        >
          Transacciones
        </button>
        <button 
          className={`tab-button ${activeTab === 'nfts' ? 'active' : ''}`}
          onClick={() => setActiveTab('nfts')}
        >
          Mis NFTs
        </button>
      </div>

      {activeTab === 'wallet' && wallet && (
        <div className="wallet-section">
          <div className="wallet-card">
            <h3>Mi Wallet</h3>
            <div className="wallet-info">
              <div className="info-row">
                <span className="info-label">Dirección:</span>
                <span className="info-value address">
                  {wallet.address}
                  <button 
                    className="copy-button"
                    onClick={() => {
                      navigator.clipboard.writeText(wallet.address);
                      setMessage({ text: 'Dirección copiada al portapapeles', type: 'success' });
                    }}
                  >
                    Copiar
                  </button>
                </span>
              </div>
              <div className="info-row">
                <span className="info-label">Clave privada:</span>
                <span className="info-value private-key">
                  {showPrivateKey ? wallet.privateKey : '••••••••••••••••••••••••••••••••••••••••••••••••••••••••••••••••'}
                  <button 
                    className="toggle-button"
                    onClick={() => setShowPrivateKey(!showPrivateKey)}
                  >
                    {showPrivateKey ? 'Ocultar' : 'Mostrar'}
                  </button>
                </span>
              </div>
              <div className="info-row">
                <span className="info-label">Balance:</span>
                <span className="info-value balance">
                  {wallet.balance} OroTokens
                </span>
              </div>
            </div>

            <div className="convert-section">
              <h4>Convertir Monedas a OroTokens</h4>
              <div className="convert-form">
                <input 
                  type="number" 
                  className="convert-input"
                  value={convertAmount}
                  onChange={(e) => setConvertAmount(Number(e.target.value))}
                  placeholder="Cantidad a convertir"
                  min="1"
                  max={coins}
                />
                <button 
                  className="convert-button"
                  onClick={handleConvertCoins}
                  disabled={convertAmount <= 0 || convertAmount > coins}
                >
                  Convertir
                </button>
              </div>
              <p className="convert-note">
                Nota: La conversión es 1:1. Cada moneda se convierte en 1 OroToken.
              </p>
            </div>
          </div>
        </div>
      )}

      {activeTab === 'transactions' && (
        <div className="transactions-section">
          <h3>Historial de Transacciones</h3>
          {transactions.length === 0 ? (
            <p className="no-history">No tienes transacciones aún.</p>
          ) : (
            <div className="transactions-list">
              {transactions.slice().reverse().map(tx => (
                <div key={tx.id} className="transaction-card">
                  <div className="transaction-header">
                    <span className={`status-badge ${tx.status}`}>
                      {tx.status === 'completed' ? 'Completada' : 
                       tx.status === 'pending' ? 'Pendiente' : 'Fallida'}
                    </span>
                    <span className="transaction-date">{formatDate(tx.timestamp)}</span>
                  </div>
                  <div className="transaction-details">
                    <div className="transaction-row">
                      <span className="transaction-label">De:</span>
                      <span className="transaction-value">
                        {tx.from === 'app_treasury' ? 'Tesoro de la Aplicación' : truncateAddress(tx.from)}
                      </span>
                    </div>
                    <div className="transaction-row">
                      <span className="transaction-label">Para:</span>
                      <span className="transaction-value">{truncateAddress(tx.to)}</span>
                    </div>
                    <div className="transaction-row">
                      <span className="transaction-label">Cantidad:</span>
                      <span className="transaction-value amount">{tx.amount} OroTokens</span>
                    </div>
                    <div className="transaction-row">
                      <span className="transaction-label">Hash:</span>
                      <span className="transaction-value hash">
                        {tx.hash.substring(0, 10)}...{tx.hash.substring(tx.hash.length - 6)}
                      </span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      )}

      {activeTab === 'nfts' && (
        <div className="nfts-section">
          <h3>Mis NFTs</h3>
          {nfts.length === 0 ? (
            <div className="no-nfts">
              <p>No tienes NFTs en tu wallet.</p>
              <p>Puedes adquirir NFTs en la tienda con tus OroTokens.</p>
            </div>
          ) : (
            <div className="nfts-grid">
              {nfts.map(nft => (
                <div key={nft.id} className="nft-card">
                  <div className="nft-image">
                    <span role="img" aria-label={nft.name}>{nft.metadata.image}</span>
                  </div>
                  <div className="nft-details">
                    <h4>{nft.name}</h4>
                    <p>{nft.metadata.description}</p>
                    <div className="nft-attributes">
                      {nft.metadata.attributes.map((attr, index) => (
                        <div key={index} className="nft-attribute">
                          <span className="attribute-type">{attr.trait_type}:</span>
                          <span className="attribute-value">{attr.value}</span>
                        </div>
                      ))}
                    </div>
                    <div className="nft-footer">
                      <span className="nft-date">Obtenido: {formatDate(nft.createdAt)}</span>
                      <span className="nft-id">#{nft.id.substring(4)}</span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default Blockchain; 