interface User {
  cedula: string;
  name: string;
  username: string;
  email: string;
  password: string;
  isFirstLogin: boolean;
  isAdmin?: boolean;
}

// LocalStorage keys
const USER_KEY = 'math_basics_users';
const IS_AUTHENTICATED_KEY = 'math_basics_authenticated';

// Function to initialize a user if it doesn't exist
export const initializeUser = (cedula: string, email: string = ''): User => {
  return {
    cedula,
    name: '',
    username: cedula, // Inicialmente, el username es la cédula
    email: email, // Usar el email proporcionado o vacío
    password: cedula, // Inicialmente, password es la misma que cedula
    isFirstLogin: true,
  };
};

// Find user by cedula
export const findUserByCedula = (cedula: string): User | null => {
  const users = getAllUsers();
  return users.find(user => user.cedula === cedula) || null;
};

// Find user by username
export const findUserByUsername = (username: string): User | null => {
  const users = getAllUsers();
  return users.find(user => user.username === username) || null;
};

// Find user by email
export const findUserByEmail = (email: string): User | null => {
  const users = getAllUsers();
  return users.find(user => user.email === email) || null;
};

// Check if a user exists
export const userExists = (cedula: string): boolean => {
  const users = getAllUsers();
  return users.some(user => user.cedula === cedula);
};

// Get all users from localStorage
export const getAllUsers = (): User[] => {
  const usersJSON = localStorage.getItem(USER_KEY);
  return usersJSON ? JSON.parse(usersJSON) : [];
};

// Save all users to localStorage
const saveAllUsers = (users: User[]): void => {
  localStorage.setItem(USER_KEY, JSON.stringify(users));
};

// Save a user (create or update)
export const saveUser = (user: User): void => {
  const users = getAllUsers();
  const existingUserIndex = users.findIndex(u => u.cedula === user.cedula);
  
  if (existingUserIndex >= 0) {
    // Update existing user
    users[existingUserIndex] = user;
  } else {
    // Add new user
    users.push(user);
  }
  
  saveAllUsers(users);
};

// Authenticate a user
export const loginUser = (cedula: string, password: string): User | null => {
  const users = getAllUsers();
  const user = users.find(u => u.cedula === cedula);
  
  if (!user) {
    return null;
  }
  
  if (user.password === password) {
    // Set authentication status
    localStorage.setItem(IS_AUTHENTICATED_KEY, 'true');
    return user;
  }
  
  return null;
};

// Complete user registration (first login)
export const completeFirstLogin = (cedula: string, name: string, username: string, email: string, newPassword: string): User | null => {
  const users = getAllUsers();
  const userIndex = users.findIndex(u => u.cedula === cedula);
  
  if (userIndex < 0) {
    return null;
  }
  
  // Update user information
  users[userIndex].name = name;
  users[userIndex].username = username;
  users[userIndex].email = email;
  users[userIndex].password = newPassword;
  users[userIndex].isFirstLogin = false;
  
  saveAllUsers(users);
  localStorage.setItem(IS_AUTHENTICATED_KEY, 'true');
  
  return users[userIndex];
};

// Check if a username is already in use
export const isUsernameInUse = (username: string, excludeCedula?: string): boolean => {
  const users = getAllUsers();
  return users.some(user => user.username === username && user.cedula !== excludeCedula);
};

// Check if an email is already in use
export const isEmailInUse = (email: string, excludeCedula?: string): boolean => {
  const users = getAllUsers();
  return users.some(user => user.email === email && user.cedula !== excludeCedula);
};

// Reset password by email
export const resetPasswordByEmail = (email: string, newPassword: string): boolean => {
  const users = getAllUsers();
  const userIndex = users.findIndex(u => u.email === email);
  
  if (userIndex < 0) {
    return false;
  }
  
  users[userIndex].password = newPassword;
  saveAllUsers(users);
  
  return true;
}

// Generate reset token (simulated)
export const generateResetToken = (email: string): string | null => {
  const user = findUserByEmail(email);
  if (!user) {
    return null;
  }
  
  // En una aplicación real, aquí generaríamos un token seguro y lo guardaríamos
  // Para este demo, simplemente usamos un string basado en la cédula y timestamp
  const token = `reset_${user.cedula}_${Date.now()}`;
  
  // En una app real, guardaríamos este token en la base de datos
  // Para este demo, lo guardamos en localStorage
  localStorage.setItem(`reset_token_${user.cedula}`, token);
  
  return token;
};

// Validate reset token (simulated)
export const validateResetToken = (token: string): User | null => {
  // En una app real, verificaríamos el token en la base de datos
  // Para este demo, extraemos la cédula del token
  const parts = token.split('_');
  if (parts.length !== 3 || parts[0] !== 'reset') {
    return null;
  }
  
  const cedula = parts[1];
  const storedToken = localStorage.getItem(`reset_token_${cedula}`);
  
  if (storedToken !== token) {
    return null;
  }
  
  // Token válido, buscar el usuario
  const users = getAllUsers();
  return users.find(u => u.cedula === cedula) || null;
};

// Check if user is authenticated
export const isAuthenticated = (): boolean => {
  return localStorage.getItem(IS_AUTHENTICATED_KEY) === 'true';
};

// Get current user
export const getCurrentUser = (): User | null => {
  if (!isAuthenticated()) {
    return null;
  }
  
  const loggedInUserJSON = localStorage.getItem('current_user');
  return loggedInUserJSON ? JSON.parse(loggedInUserJSON) : null;
};

// Save current user
export const saveCurrentUser = (user: User): void => {
  localStorage.setItem('current_user', JSON.stringify(user));
};

// Logout user
export const logoutUser = (): void => {
  localStorage.removeItem(IS_AUTHENTICATED_KEY);
  localStorage.removeItem('current_user');
};

// Change password
export const changePassword = (cedula: string, oldPassword: string, newPassword: string): boolean => {
  const users = getAllUsers();
  const userIndex = users.findIndex(u => u.cedula === cedula);
  
  if (userIndex < 0 || users[userIndex].password !== oldPassword) {
    return false;
  }
  
  users[userIndex].password = newPassword;
  saveAllUsers(users);
  
  // Update current user if it's the logged in user
  const currentUser = getCurrentUser();
  if (currentUser && currentUser.cedula === cedula) {
    currentUser.password = newPassword;
    saveCurrentUser(currentUser);
  }
  
  return true;
}; 