import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { 
  loginUser, 
  logoutUser, 
  isAuthenticated, 
  getCurrentUser, 
  saveCurrentUser,
  completeFirstLogin,
  initializeUser,
  saveUser,
  userExists,
  changePassword,
  findUserByUsername,
  findUserByEmail,
  findUserByCedula,
  generateResetToken,
  validateResetToken,
  resetPasswordByEmail
} from '../services/authService';

interface User {
  cedula: string;
  name?: string;
  username?: string;
  email?: string;
  registrationComplete: boolean;
  isAdmin?: boolean;
}

interface AuthResult {
  success: boolean;
  error?: string;
}

interface PasswordResetResult {
  success: boolean;
  token?: string;
  error?: string;
}

interface AuthContextType {
  user: User | null;
  loading: boolean;
  login: (identifier: string, password: string, isNewUser?: boolean, email?: string) => Promise<AuthResult>;
  completeRegistration: (name: string, username: string, email: string, newPassword: string) => Promise<AuthResult>;
  logout: () => void;
  requestPasswordReset: (email: string) => Promise<PasswordResetResult>;
  resetPassword: (token: string, newPassword: string) => Promise<AuthResult>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const storedUser = localStorage.getItem('user');
    
    if (storedUser) {
      try {
        setUser(JSON.parse(storedUser));
      } catch (error) {
        console.error('Error al parsear usuario del localStorage:', error);
        localStorage.removeItem('user');
      }
    }
    
    setLoading(false);
  }, []);

  useEffect(() => {
    if (user) {
      localStorage.setItem('user', JSON.stringify(user));
    } else {
      localStorage.removeItem('user');
    }
  }, [user]);

  const login = async (identifier: string, password: string, isNewUser: boolean = false, email: string = ''): Promise<AuthResult> => {
    await new Promise(resolve => setTimeout(resolve, 800));
    
    let loggedInUser;
    
    if (isNewUser) {
      // Para nuevos usuarios, el identificador es la cédula
      if (!userExists(identifier)) {
        const newUser = initializeUser(identifier, email);
        saveUser(newUser);
      } else {
        // Si ya existe, actualizar el email si se proporciona
        if (email) {
          const existingUser = findUserByCedula(identifier);
          if (existingUser) {
            existingUser.email = email;
            saveUser(existingUser);
          }
        }
      }
      
      loggedInUser = loginUser(identifier, password);
    } else {
      // Para usuarios existentes, el identificador es el nombre de usuario
      const foundUser = findUserByUsername(identifier);
      
      if (foundUser) {
        // Verificar contraseña con la función original de login
        loggedInUser = loginUser(foundUser.cedula, password);
      }
    }
    
    if (loggedInUser) {
      const userForState: User = {
        cedula: loggedInUser.cedula,
        name: loggedInUser.name || '',
        username: loggedInUser.username || loggedInUser.cedula,
        email: loggedInUser.email || '',
        registrationComplete: !loggedInUser.isFirstLogin,
        isAdmin: loggedInUser.isAdmin || false
      };
      
      setUser(userForState);
      saveCurrentUser(loggedInUser);
      return { success: true };
    } else {
      return isNewUser 
        ? { success: false, error: 'Cédula o contraseña incorrecta' }
        : { success: false, error: 'Nombre de usuario o contraseña incorrecta' };
    }
  };

  const logout = () => {
    logoutUser();
    setUser(null);
  };

  const completeRegistration = async (name: string, username: string, email: string, newPassword: string): Promise<AuthResult> => {
    if (!user) {
      return { success: false, error: 'No hay sesión activa' };
    }

    await new Promise(resolve => setTimeout(resolve, 800));

    // Verificar si el nombre de usuario ya existe
    const existingUser = findUserByUsername(username);
    if (existingUser && existingUser.cedula !== user.cedula) {
      return { success: false, error: 'Este nombre de usuario ya está en uso. Por favor elija otro.' };
    }
    
    // Verificar si el email ya existe
    const existingEmail = findUserByEmail(email);
    if (existingEmail && existingEmail.cedula !== user.cedula) {
      return { success: false, error: 'Este correo electrónico ya está registrado. Por favor utilice otro.' };
    }

    if (userExists(user.cedula)) {
      const updatedUser = { ...user, name, username, email, registrationComplete: true };
      
      // Actualizar el servicio con el nombre de usuario
      const serviceUser = completeFirstLogin(user.cedula, name, username, email, newPassword);
      
      if (serviceUser) {
        setUser(updatedUser);
        return { success: true };
      }
      return { success: false, error: 'Error al actualizar usuario' };
    }

    return { success: false, error: 'Usuario no encontrado' };
  };
  
  const requestPasswordReset = async (email: string): Promise<PasswordResetResult> => {
    await new Promise(resolve => setTimeout(resolve, 800));
    
    const user = findUserByEmail(email);
    if (!user) {
      return { success: false, error: 'No se encontró ningún usuario con este correo electrónico' };
    }
    
    const token = generateResetToken(email);
    if (!token) {
      return { success: false, error: 'Error al generar el token de recuperación' };
    }
    
    // En una aplicación real, aquí enviaríamos un correo con el token
    // Para este demo, simplemente devolvemos el token para mostrarlo en la interfaz
    
    return { 
      success: true, 
      token: token,
      // Incluir información que normalmente iría en el correo
      error: `SIMULACIÓN DE CORREO: Se ha enviado un enlace de recuperación a ${email}. Su nombre de usuario es: ${user.username}`
    };
  };
  
  const resetPassword = async (token: string, newPassword: string): Promise<AuthResult> => {
    await new Promise(resolve => setTimeout(resolve, 800));
    
    const user = validateResetToken(token);
    if (!user) {
      return { success: false, error: 'El enlace de recuperación es inválido o ha expirado' };
    }
    
    if (resetPasswordByEmail(user.email, newPassword)) {
      return { success: true };
    }
    
    return { success: false, error: 'Error al restablecer la contraseña' };
  };

  const value = {
    user,
    loading,
    login,
    logout,
    completeRegistration,
    requestPasswordReset,
    resetPassword
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};

export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}; 