import React, { useState, useRef } from 'react';
import './PDFUploader.css';

interface PDFUploaderProps {
  onFileUpload: (file: File) => void;
  maxSize?: number; // Tamaño máximo en MB, por defecto 10MB
  acceptMultiple?: boolean;
}

const PDFUploader: React.FC<PDFUploaderProps> = ({ 
  onFileUpload, 
  maxSize = 10,
  acceptMultiple = false
}) => {
  const [dragActive, setDragActive] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);
  
  const handleDrag = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
    
    if (e.type === 'dragenter' || e.type === 'dragover') {
      setDragActive(true);
    } else if (e.type === 'dragleave') {
      setDragActive(false);
    }
  };
  
  const validateFile = (file: File): boolean => {
    // Verificar tipo de archivo
    if (file.type !== 'application/pdf') {
      setError(`El archivo ${file.name} no es un PDF válido.`);
      return false;
    }
    
    // Verificar tamaño
    const fileSizeMB = file.size / (1024 * 1024);
    if (fileSizeMB > maxSize) {
      setError(`El archivo ${file.name} excede el tamaño máximo permitido de ${maxSize}MB.`);
      return false;
    }
    
    setError(null);
    return true;
  };
  
  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
    setDragActive(false);
    
    if (e.dataTransfer.files && e.dataTransfer.files.length > 0) {
      // Si no se permiten múltiples archivos, usar solo el primero
      if (!acceptMultiple && e.dataTransfer.files.length > 1) {
        setError('Solo se permite cargar un archivo PDF a la vez.');
        return;
      }
      
      const file = e.dataTransfer.files[0];
      if (validateFile(file)) {
        onFileUpload(file);
      }
    }
  };
  
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    
    if (e.target.files && e.target.files.length > 0) {
      // Si no se permiten múltiples archivos, usar solo el primero
      if (!acceptMultiple && e.target.files.length > 1) {
        setError('Solo se permite cargar un archivo PDF a la vez.');
        return;
      }
      
      const file = e.target.files[0];
      if (validateFile(file)) {
        onFileUpload(file);
      }
    }
  };
  
  const handleButtonClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click();
    }
  };
  
  return (
    <div className="pdf-uploader-container">
      <div 
        className={`pdf-uploader-droparea ${dragActive ? 'pdf-uploader-active' : ''}`}
        onDragEnter={handleDrag}
        onDragOver={handleDrag}
        onDragLeave={handleDrag}
        onDrop={handleDrop}
      >
        <input 
          ref={fileInputRef}
          type="file" 
          className="pdf-uploader-input"
          onChange={handleChange}
          accept=".pdf"
          multiple={acceptMultiple}
        />
        
        <div className="pdf-uploader-content">
          <div className="pdf-uploader-icon">📄</div>
          <p className="pdf-uploader-text">
            Arrastra y suelta un archivo PDF aquí o
          </p>
          <button 
            className="pdf-uploader-button"
            onClick={handleButtonClick}
          >
            Seleccionar archivo
          </button>
          <p className="pdf-uploader-note">
            Solo archivos PDF (máx. {maxSize}MB)
          </p>
        </div>
      </div>
      
      {error && (
        <div className="pdf-uploader-error">
          {error}
        </div>
      )}
    </div>
  );
};

export default PDFUploader; 